import { useEffect, useState } from "react";

import {
    ChevronDownIcon,
    LinkIcon,
    LoginIcon,
    ShoppingCartIcon,
    MenuIcon,
    ChevronUpIcon
} from "@heroicons/react/solid";
import { useForm, useWatch } from "react-hook-form";
import { MobileDropDownI, MobileLinkI } from "../../interfaces";
import { useGet } from "../../hooks/useGet";
import { RecentStores } from "../../models";

const Header = () => {
    const [opened, setOpened] = useState(false);
    const revertOpened = () => setOpened((prevState) => !prevState);
    return (
        <div className="flex font-mulish px-16 font-bold text-sm w-full h-24 content-between justify-between items-center">
            <OdysseaImage />
            <DropDownMenus opened={opened} />
            <IconButtons revertOpened={revertOpened} />
        </div>
    );
};

const OdysseaImage = () => {
    return (
        <a href="https://odyssea.io" className="w-1/6">
            <img src="https://odyssea.io/wp-content/uploads/2021/11/cropped-Color-logo-no-background.png" />
        </a>
    );
};

const DropDownMenus = ({ opened }: { opened: boolean }) => {
    const nfts = [
        { url: "view-my-nfts", label: "View my NFTs" },
        { url: "create-an-item", label: "Create An Item" }
    ];

    const categories = [
        { url: "product-category/art", label: "Art" },
        { url: "product-category/watches", label: "Watches" },
        { url: "product-category/wine", label: "Wine" },
        { url: "product-category/supercars", label: "Supercars" },
        { url: "product-category/securities", label: "Securities" },
        { url: "product-category/collectibles", label: "Collectibles" }
    ];
    return (
        <>
            <div className="hidden lg:flex justify-end tracking-[0.14em] w-8/12">
                <MenuLink label="Browse" url="asd " />
                <DropDownMenu label="Nfts" contentLinks={nfts} />
                <DropDownMenu label="Categories" contentLinks={categories} />
                <ExplorerVendors label="Store List" contentLinks={[]} />
            </div>
            <div
                className={`${
                    opened ? "flex" : "hidden"
                } lg:hidden absolute top-[80px] w-full z-10 left-0 flex-col flex items-center`}
            >
                <MobileLink label="Browse" url="as" />
                <MobileLink label="Create an Item" url="as" />
                <MobileDropDown label="Categories" links={categories} />
                <MobileLink label="Explore stores" url="as" />
            </div>
        </>
    );
};

const MobileLink = ({ label, url }: MobileLinkI) => {
    return (
        <div className="shadow w-full text-[#0c5adb] flex justify-center text-2xl uppercase tracking-[0.14em] h-16 items-center">
            <a href={url}>{label}</a>
        </div>
    );
};

const MobileDropDown = ({ label, links }: MobileDropDownI) => {
    const [opened, setOpened] = useState(false);

    const onClickHandle = () => {
        setOpened((prevState) => !prevState);
    };

    return (
        <>
            <div
                className="shadow w-full text-[#0c5adb] flex justify-center text-2xl uppercase tracking-[0.14em] h-16 items-center"
                onClick={onClickHandle}
            >
                {label} {opened ? <ChevronUpIcon className="w-7" /> : <ChevronDownIcon className="w-7" />}
            </div>
            {opened &&
                links.map((link) => (
                    <MobileLink key={link.label} url={`product-category/${link.url}`} label={link.label} />
                ))}
        </>
    );
};

const IconButtons = ({ revertOpened }: { revertOpened: () => void }) => {
    return (
        <div className="flex justify-around tracking-[0.14em] w-4/6 sm:w-1/2 lg:w-1/6 items-center">
            <div className="flex justify-center items-center bg-[#0c5adb] text-white leading-6 h-20 w-20 rounded">
                <LinkIcon className="w-9" />
            </div>

            <div className="flex justify-center items-center bg-[#0c5adb] text-white leading-6 h-20 w-20 rounded">
                <LoginIcon className="w-9" />
            </div>

            <ShoppingCartIcon className="w-10 text-[#0c5adb]" />

            <MenuIcon className="block lg:hidden w-10 text-[#0c5adb]" onClick={revertOpened} />
        </div>
    );
};

const DropDownMenu = ({ label, contentLinks }: { label: string; contentLinks: MobileLinkI[] }) => (
    <div className="flex group items-center text-[13px] uppercase mx-8">
        {label} <ChevronDownIcon className="w-7" />
        <div className="group-hover:flex w-80 hidden flex-col top-60 absolute bg-white z-50 shadow-xl border-solid border border-[#b7b7b733] rounded">
            {contentLinks.map((link) => (
                <a
                    className="flex justify-around items-center text-black py-6 px-7 hover:bg-[#EEEEEE]"
                    key={link.label}
                    href={link.url}
                >
                    {link.label}
                </a>
            ))}
        </div>
    </div>
);

const MenuLink = ({ label, url }: MobileLinkI) => (
    <a href={url} className="flex items-center text-[13px] uppercase mx-8">
        {label}
    </a>
);

const ExplorerVendors = ({
    label,
    contentLinks
}: {
    label: string;
    contentLinks: { label: string; url: string }[];
}) => {
    const [vendors, setVendors] = useState([]);
    const [showedVendors, setShowedVendors] = useState([]);
    const [lastVisitedVendors, setLastVisitedVendors] = useState([]);
    /*useEffect(async () => {
        const response = await axios.get(
            `${window.location.origin}/wp-json/dokan/v1/stores`,
        )
        setVendors(response.data)
        setShowedVendors(response.data)
    }, []);*/

    const { data } = useGet<RecentStores>(`/dokan/stores`);
    const { data: x } = useGet<RecentStores>(`${window.location.origin}/wp-json/dokan/v1/stores`);
    console.log(x);
    const { control, register } = useForm();
    const vendorsName = "vendors";
    const second = useWatch({
        control,
        name: vendorsName
    });

    useEffect(() => {
        if (second !== "" || !second) {
            setShowedVendors(
                vendors
                    .filter((vendor) => vendor.store_name.toLowerCase().indexOf(second.toLowerCase()) !== -1)
                    .slice(0, 5)
            );
        } else {
            setShowedVendors([]);
        }
    }, [second]);
    return (
        <div className="flex group  items-center text-[13px] uppercase">
            {label} <ChevronDownIcon className="w-7" />
            <div className="group-hover:flex w-80 hidden flex-col top-60 absolute bg-white z-50 shadow-xl border-solid border border-[#b7b7b733] rounded">
                <a
                    className="flex justify-around items-center text-black py-6 px-7 hover:bg-[#EEEEEE]"
                    href="https://odyssea.io/store-listing"
                >
                    Explore Stores
                </a>
                <div className="text-black w-full hover:bg-[#EEEEEE]">
                    <input
                        className="focus:shadow-xl  tracking-[0.14em] uppercase outline-none text-[13px] text-center text-[#222222] fill-[#222222] h-12 font-mulish font-bold border border-none border-[#ebeced] bg-white w-full placeholder:text-center placeholder:text-black"
                        {...register(vendorsName)}
                        placeholder="Find vendors"
                    />
                </div>
                {data?.recentStores.map((store) => (
                    <a
                        key={store.storeName}
                        href={store.shopUrl}
                        className="flex items-center text-black py-6 px-7 hover:bg-[#EEEEEE] justify-around"
                    >
                        <img className="w-8 h-8 rounded-full" src={store.banner} />
                        {store.storeName}
                    </a>
                ))}
            </div>
        </div>
    );
};

export { Header };
